#page-container {
  position: relative;
  min-height: 100vh;
}

#content-wrap {
  padding-bottom: 50px; /* Footer height */
}

#footer {
  color: #282a36;
  font-size: 15px;
  font-family: Hack;
  font-weight: bold;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
}

#latest {
  font-family: Hack;
}
.slick-prev:before {
  color: #ffb86c !important;
  background-color: #fff;
}
.slick-next:before {
  color: #ffb86c !important;
  background-color: #fff;
}

.slick-dots {
  height: 14px;
}

#summary {
  font-family: Hack;
}

.slick-slider {
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
}
